@font-face {
  font-family: "Comic Sans MS";
  src: url("assets/fonts/comic-sans-ms/COMIC.TTF");
  font-style: normal;
}
@font-face {
  font-family: "Comic Sans MS";
  src: url("assets/fonts/comic-sans-ms/comici.ttf");
  font-style: italic;
}
@font-face {
  font-family: "Comic Sans MS";
  src: url("assets/fonts/comic-sans-ms/comicz.ttf");
  font-style: italic;
  font-weight: bold;
}
@font-face {
  font-family: "Comic Sans MS";
  src: url("assets/fonts/comic-sans-ms/design.graffiti.comicsansmsgras.ttf");
  font-style: normal;
  font-weight: bold;
}
#body-user #header,
#body-settings #header,
#body-public #header {
  background-color: #ed8b5e !important;
  background-image: none !important;
}

#body-user #app-content {
  background-image: url("../../assets/img/logo.png");
  background-position: left bottom;
  background-repeat: no-repeat;
  background-size: 150px;
}

#body-login {
  background-image: url("../../assets/img/background.jpg") !important;
  background-color: #ed8b5e !important;
  background-repeat: no-repeat;
  background-position: bottom center;
}
@media only screen and (max-width: 1200px) {
  #body-login {
    background-image: none !important;
  }
}
#body-login .wrapper {
  margin-top: 20vh;
}
#body-login .wrapper main form fieldset {
  margin-top: 0;
}
#body-login .wrapper main form input:first-child {
  margin-top: 0;
}
#body-login .wrapper main .lost-password-container {
  margin-top: 0;
}
#body-login #header .logo {
  visibility: hidden;
  background-image: url("../../assets/img/logo.png") !important;
  border-bottom: #fff solid 6px;
}
@media only screen and (max-width: 1200px) {
  #body-login #header .logo {
    visibility: visible;
  }
}
#body-login.login-page-has-form #header {
  max-height: 200px;
}
#body-login.login-page-has-form #header .logo {
  margin-bottom: 0 !important;
  border-bottom: none;
}

body {
  font-family: "Comic Sans MS", -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Cantarell, Ubuntu, Helvetica Neue, Arial, Noto Color Emoji, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol !important;
}